<template>
  <div>
    <el-table
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
      <el-table-column
          label="编号"
          width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="姓名"
          width="180" prop="name">

      </el-table-column>
      <el-table-column
          label="生日"
          width="180" prop="birthday">

      </el-table-column>
      <el-table-column
          label="性别"
          width="180" prop="sex">

      </el-table-column>
      <el-table-column
          label="地址"
          width="350" prop="address">

      </el-table-column>
      <el-table-column  align="right">
        <template slot="header" slot-scope="scope">
          <el-input
              v-model="search"
              size="mini"
              placeholder="输入关键字搜索"/>
        </template>
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

<!--    分页组件-->

    <el-row>
      <el-col :span="12" :offset="12">
      <el-pagination style="margin: 10px 0px"
          layout="prev, pager, next ,jumper,total,sizes"
          :total="totals" background small :page-sizes="[2,4,6,8]" :page-size="size" :current-page="pageNow"
          @size-change="findSize"
           @current-change="findPage"
      >
      </el-pagination>
      </el-col>
    </el-row>
    <el-button type="success" style="margin-top: 5px" size="mini" @click="dialogFormVisible = true">添加</el-button>

    <el-dialog title="添加用户信息" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules" >
        <el-form-item label="姓名：" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别：">
          <el-radio-group v-model="form.sex">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日" prop="bir">
          <el-date-picker value-format="yyyy-MM-dd" type="date" v-model="form.birthday" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="住址" prop="address">
          <el-input type="textarea" v-model="form.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="add('form')">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改用户信息" :visible.sync="dialogUpdateFormVisible">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="姓名：" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别：">
          <el-radio-group v-model="form.sex">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日" prop="bir">
          <el-date-picker v-model="form.birthday" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="住址"  prop="address">
          <el-input type="textarea" v-model="form.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="update('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
import axios from "axios";

export default {
  name: "List",
  data() {
    return {
      tableData: [],
      search: '',
      dialogFormVisible: false,
      dialogUpdateFormVisible : false,
      form: {
        name: '',
        sex:'男',
        birthday:'',
        address:''
      },
      formLabelWidth: '120px',
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
        ],
        bir: [
          { required: true, message: '请输入生日', trigger: 'blur' },
        ],
        address: [
          { required: true, message: '请输入住址', trigger: 'blur' },
        ],

      },
      totals:0,
      pageNow:1,
      size:2
    }
  },
  methods: {
    findPage(page){
      this.pageNow = page;
      this.getAllByPage(page,this.size)
    },
    findSize(size){
      this.size = size;
      this.getAllByPage(this.pageNow,size)
    },
    getAllByPage(pageNow,size){
      pageNow = pageNow == undefined ? this.pageNow : pageNow
      size = size == undefined ? this.size : size
      this.$axios.get("http://localhost:8090/users/findByPage",{params:{
          pageNow:pageNow,
          size:size
        }}).then(res=>{
        this.tableData = res.data.users;
        this.totals = res.data.totals;
      })
    },
    handleEdit(index, row) {
      this.dialogUpdateFormVisible = true
      this.$axios.get("http://localhost:8090/users/getbyid",{params:{
        id:row.id
        }}).then(res=>{
        this.form = res.data.data
      })
      // this.$axios.post("http://localhost:8090/users/update",this.form).then(res=>{
      //   this.form = res.data.data
      // })
    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除条数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {

          this.$axios.get("http://localhost:8090/users/delete",{
            params:{
              id:row.id
            }
          }).then(res=>{
            if(res.data.status == 20041){
              this.getAllByPage()
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'danger',
                message: '删除失败!'
              });
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    add(form){
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.$axios.post("http://localhost:8090/users",this.form
          ).then(res=>{
            if(res.data.status == 20021){
              this.form={
                sex:'男'
              }
              this.dialogFormVisible = false
              this.$message({
                message:"恭喜你，添加成功",
                type:"success"
              })
              this.getAllByPage()
            }
          })
        } else {
          this.$message.error("输入的数据不合法")
          return false;
        }
      });

    },
    update(form){
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.$axios.post("http://localhost:8090/users/update",this.form).then(res=>{
            if(res.data.status == 20031){
              this.getAllByPage()
              this.dialogUpdateFormVisible = false
              this.$message({
                message:"修改成功",
                type:"success"
              })
            }
          })
        } else {
          this.$message.error("输入的数据不合法")
          return false;
        }
      });

    }
  },
  created() {
    this.getAllByPage()
  },


}
</script>

<style scoped>

</style>