import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from "../views/Index";
import List from "@/views/user/List";

Vue.use(VueRouter)

const routes = [
  {path:'/',redirect:'/index'},
  {path:'/index',component:Index},
  {path: '/list',component: List}
]

const router = new VueRouter({
  routes
})

export default router
